import { menuType } from '@/utils/types';
import BusinessIcon from '@mui/icons-material/Business';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import { Navigate, useLocation, useNavigate } from 'react-router';

import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import PlaceIcon from '@mui/icons-material/Place';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import LocalAtmIcon from '@mui/icons-material/LocalAtm';

interface Props {
  expanded: boolean;
}

const menu: menuType[] = [
  {
    icon: <BusinessIcon />,
    text: 'Agencies',
  },
  {
    icon: <DirectionsCarIcon />,
    text: 'Routes',
  },
  {
    icon: <PlaceIcon />,
    text: 'Stops',
  },
  {
    icon: <CalendarMonthIcon />,
    text: 'Calendar',
  },
  {
    icon: <LocalAtmIcon />,
    text: 'Fares',
  },
];

const Sidebar: React.FC<Props> = ({ expanded }) => {
  const location = useLocation();
  const navigate = useNavigate();

  const { pathname } = location;

  const ButtonSelected = (text: string) =>
    pathname.toLowerCase().includes(text.toLowerCase());

  const handleListItemClick = (event: string) => {
    !ButtonSelected(event) ? navigate(`/${event}`) : navigate(`/`);
  };

  return (
    <div className="h-full border-r  ">
      <List>
        {menu.map((element) => (
          <ListItem key={element.text} disableGutters disablePadding>
            <ListItemButton
              disableGutters
              selected={ButtonSelected(element.text)}
              onClick={() => handleListItemClick(element.text)}
            >
              <ListItemIcon sx={{ justifyContent: 'center' }}>
                {element.icon}
              </ListItemIcon>
              {expanded && (
                <ListItemText
                  primary={element.text}
                  sx={{ marginRight: '100px' }}
                />
              )}
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </div>
  );
};
export default Sidebar;
