// features/agencySlice.ts
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { agencyType } from '@/utils/types';

interface AgencyState {
  agencies: agencyType[];
}

const initialState: AgencyState = {
  agencies: [],
};

const agencySlice = createSlice({
  name: 'agency',
  initialState,
  reducers: {
    setAgencies: (state, action: PayloadAction<agencyType[]>) => {
      state.agencies = action.payload;
    },
    addAgency: (state, action: PayloadAction<agencyType>) => {
      state.agencies.push(action.payload);
    },
    removeAgency: (state, action: PayloadAction<string>) => {
      state.agencies = state.agencies.filter(
        (agency) => agency._id !== action.payload
      );
    },
    editAgency: (state, action: PayloadAction<agencyType>) => {
      const index = state.agencies.findIndex(
        (agency) => agency._id === action.payload._id
      );
      if (index !== -1) {
        state.agencies[index] = action.payload;
      }
    },
  },
});

export const { setAgencies, addAgency, removeAgency, editAgency } =
  agencySlice.actions;
export default agencySlice.reducer;
