import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  IconButton,
  ListItem,
  ListItemButton,
  ListItemText,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate } from 'react-router';
import DeleteIcon from '@mui/icons-material/Delete';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '@/store/store';

const StopList = () => {
  const navigate = useNavigate();
  const stops = useSelector((state: RootState) => state.stops.stops);

  const [open, setOpen] = useState(false);
  const [stopId, setStopId] = useState<{ id?: string; name: string }>({
    name: '',
    id: '',
  });

  return (
    <div className="min-w-44">
      <div className="mr-2">
        {stops?.map((element) => (
          <ListItem
            key={element.stop_name}
            sx={{
              position: 'relative',
              '&:hover .hover-button': {
                opacity: 1,
                visibility: 'visible',
              },
            }}
            disableGutters
            disablePadding
            secondaryAction={
              <IconButton
                className="hover-button"
                edge="end"
                aria-label="delete"
                onClick={() => {
                  setOpen(true);
                  setStopId({ id: element._id, name: element.stop_name! });
                }}
                sx={{
                  opacity: 0,
                  visibility: 'hidden',
                  transition: 'opacity 0.3s ease, visibility 0.3s ease',
                }}
              >
                <DeleteIcon />
              </IconButton>
            }
          >
            <ListItemButton
              disableGutters
              onClick={() => navigate(`/stops/edit?id=${element._id}`)}
            >
              <ListItemText
                primary={element.stop_name}
                sx={{ marginRight: '10px', marginLeft: '10px' }}
              />
            </ListItemButton>
          </ListItem>
        ))}
        <Dialog open={open} aria-labelledby="draggable-dialog-title">
          <DialogTitle id="draggable-dialog-title">Delete</DialogTitle>
          <DialogContent>
            <DialogContentText>
              Would you really want to delete {stopId.name}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              autoFocus
              onClick={() => {
                setStopId({ name: '', id: '' });
                setOpen(false);
              }}
            >
              Cancel
            </Button>
            <Button
              color="error"
              onClick={() => {
                navigate(`/stops/delete?id=${stopId.id}`);
                setStopId({ name: '', id: '' });
                setOpen(false);
              }}
            >
              Delete
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    </div>
  );
};
export default StopList;
