import { AppDispatch, RootState } from '@/store/store';
import { DynamicForm } from './DynamicForm';
import { agencyType, httpResponse } from '@/utils/types';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import { IconButton } from '@mui/material';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';
import { getWithExpiry } from '@/utils/localStorage';
import { addAgency, editAgency } from '@/store/agencySlice';

interface agencyFormProps {
  update: boolean;
  setResponse: (response: httpResponse) => void;
}

const field = {
  name: '',
  email: '',
  website: '',
};
type FieldKeys = keyof typeof field;

export const AgencyForm = ({ update, setResponse }: agencyFormProps) => {
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch<AppDispatch>();

  const { search } = location;
  const agencies = useSelector((state: RootState) => state.agencies.agencies);

  const [model, setModel] = useState<agencyType>(field);

  const handleUserSubmit = async (data: agencyType) => {
    const agencyId = search.split('=')[1];

    let url = '/api/agencies';
    if (update) {
      url = url + `/${agencyId}`;
    }
    const options: RequestInit = {
      method: update ? 'PUT' : 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${getWithExpiry('TOKEN')}`,
      },
      body: JSON.stringify(data),
    };

    try {
      const response = await fetch(url, options);

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const result = await response.json();

      setResponse({
        code: response.status,
        loaded: true,
        loading: true,
        message: result.message,
      });

      if (update) {
        dispatch(editAgency(result));
      } else {
        dispatch(addAgency(result));
      }
      navigate(-1);
      console.log(update ? 'Update Success:' : 'Add Success:', result);
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const getAgencyById = (agencyFiltered: agencyType[]) => {
    return agencyFiltered.map((item) =>
      Object.keys(field).reduce((acc, key) => {
        if (key in item) {
          acc[key as FieldKeys] = item[key as FieldKeys];
        }
        return acc;
      }, {} as Pick<agencyType, FieldKeys>)
    );
  };

  useEffect(() => {
    if (update) {
      const agencyId = search.split('=')[1];
      const agencyFiltered = agencies.filter((a) => a._id! === agencyId);

      const agency = getAgencyById(agencyFiltered);
      setModel(agency[0]);
    }
  }, [update]);

  return (
    <div className="m-4">
      <div className="flex mb-4">
        <IconButton
          aria-label="back"
          size="small"
          onClick={() => {
            navigate(-1);
          }}
        >
          <ArrowBackIosIcon />
        </IconButton>
        <h1 className="my-2"> {update ? 'Edit Agency' : 'Add Agency'}</h1>
      </div>
      <DynamicForm<agencyType> model={model} onSubmit={handleUserSubmit} />
    </div>
  );
};
