import { useEffect, useState } from 'react';

import { useLocation, useNavigate } from 'react-router';
import AgencyList from '@/components/AgencyList';
import { AgencyForm } from '@/components/AgencyForm';
import { getWithExpiry } from '@/utils/localStorage';
import { useDispatch } from 'react-redux';
import { removeAgency, setAgencies } from '@/store/agencySlice';
import Toast from '@/components/Toast';
import { httpResponse } from '@/utils/types';

const Agencies = () => {
  const [redirect, setRedirect] = useState<string>('list');
  const dispatch = useDispatch();

  const navigate = useNavigate();

  const location = useLocation();

  const [response, setResponse] = useState<httpResponse>({
    code: 0,
    loaded: false,
    loading: false,
    message: '',
  });

  const getAgencies = async () => {
    try {
      const response = await fetch('/api/agencies', {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: `Bearer ${getWithExpiry('TOKEN')}`,
        },
      });
      const data = await response.json();
      if (response.ok) {
        console.log(' data ,', data);
        //  setAgencies(data);
        dispatch(setAgencies(data)); // Set the agencies in Redux state
      } else {
      }
    } catch (error) {
      console.error('Network error:', error);
      // Handle network error, e.g., show a toast
    }
  };

  useEffect(() => {
    getAgencies();
  }, []);

  const locationHandler = () => {
    const { pathname } = location;
    switch (pathname.split('/')[2]) {
      case 'edit':
        setRedirect('edit');
        break;
      case 'new':
        setRedirect('new');
        break;
      case 'delete':
        setRedirect('delete');
        break;
      default:
        setRedirect('list');
        return;
    }
  };
  useEffect(() => {
    locationHandler();
  }, [location]);
  const deleteAgency = async () => {
    try {
      const { search } = location;

      const agencyId = search.split('=')[1];

      //setLoading(true);
      const response = await fetch(`/api/agencies/${agencyId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${getWithExpiry('TOKEN')}`,
        },
      });
      const data = await response.json();

      setResponse({
        code: response.status,
        loaded: true,
        loading: true,
        message: data.message,
      });
      if (!response.ok) {
        throw new Error(`Failed to delete agency: ${response.status}`);
      }

      // Remove agency from Redux store
      dispatch(removeAgency(agencyId));

      navigate('/agencies');
    } catch (error) {
      console.error('Error deleting agency:', error);
    } finally {
      //  setLoading(false);
    }
  };
  useEffect(() => {
    if (redirect == 'delete') {
      deleteAgency();
    }
  }, [redirect]);
  const ToastHandler = (result: httpResponse) => {
    setResponse({
      code: result.code,
      loaded: true,
      loading: true,
      message: result.message,
    });
  };
  return (
    <>
      <div className="flex h-full drop-shadow-xl shadow-xl">
        <div className=" h-full border-r-2 flex flex-col py-2">
          {redirect == 'list' && <AgencyList  />}
          {redirect == 'new' && (
            <AgencyForm update={false} setResponse={ToastHandler} />
          )}
          {redirect == 'edit' && (
            <AgencyForm update={true} setResponse={ToastHandler} />
          )}
        </div>
      </div>
      {response.loaded && (
        <Toast code={response.code} message={response.message} />
      )}
    </>
  );
};

export default Agencies;
