import {
  Button,
  FormControl,
  IconButton,
  TextField,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Typography,
} from '@mui/material';

import { Visibility, VisibilityOff } from '@mui/icons-material';
import { useState, FormEvent } from 'react';
import Toast from './Toast';
import { httpResponse } from '@/utils/types';
import { setWithExpiry } from '@/utils/localStorage';
import { useNavigate } from 'react-router';

interface loginErros {
  email: boolean;
  password: boolean;
}
interface loginInputs {
  email: string;
  password: String;
}

const LoginForm = () => {
  const navigate = useNavigate();

  const [showPassword, setShowPassword] = useState(false);
  const [inputs, setInputs] = useState<loginInputs>({
    email: '',
    password: '',
  });
  const [errors, setErrors] = useState<loginErros>({
    email: true,
    password: true,
  });

  const [response, setResponse] = useState<httpResponse>({
    code: 0,
    loaded: false,
    loading: false,
    message: '',
  });

  const validateEmail = (email: string) => {
    return String(email)
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
  };
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      const response = await fetch('/api/session', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(inputs),
      });
      const data = await response.json();
      console.log(' data ', data);
      setResponse({
        code: response.status,
        loaded: true,
        loading: true,
        message: data.message,
      });
      if (response.ok) {
        setWithExpiry('TOKEN', data.token.data, data.token.expiresIn);
        navigate('/');
      } else {
      }
    } catch (error) {
      console.error('Network error:', error);
      // Handle network error, e.g., show a toast
    }
  };

  const disabledSubmit =
    errors.email &&
    validateEmail(inputs.email) != null &&
    errors.password &&
    inputs.password.length > 1;

  return (
    <div className="w-5/6 lg:w-2/4 m-auto flex flex-col gap-4">
      <div className="flex flex-col gap-4">
        <h1 className="font-bold text-4xl">Sign in to emka transport system</h1>
        <Typography>Dont have an account yet?Contact us</Typography>
      </div>
      <form
        className="flex flex-col  gap-4 m-auto w-full"
        onSubmit={handleSubmit}
      >
        <TextField
          id="email-input"
          label="Email"
          variant="outlined"
          onFocus={() => {
            if (!validateEmail(inputs.email)) {
              setErrors({ ...errors, email: false });
            }
          }}
          onChange={(e) => {
            if (validateEmail(e.target.value)) {
              setErrors({ ...errors, email: true });
            }
            setInputs({ ...inputs, email: e.target.value });
          }}
          error={!errors.email}
        />

        <FormControl variant="outlined" error={!errors.password}>
          <InputLabel htmlFor="password-input">Password</InputLabel>
          <OutlinedInput
            id="password-input"
            type={showPassword ? 'text' : 'password'}
            label="Password"
            onFocus={() => {
              if (inputs.password.length < 1) {
                setErrors({ ...errors, password: false });
              }
            }}
            onChange={(e) => {
              if (e.target.value.length > 1) {
                setErrors({ ...errors, password: true });
              }
              setInputs({ ...inputs, password: e.target.value });
            }}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label={
                    showPassword ? 'hide the password' : 'display the password'
                  }
                  onClick={handleClickShowPassword}
                  edge="end"
                >
                  {showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
          />
        </FormControl>
        <a href="forget-password">
          <span className=" font-bold text-blue-500 ">Forgot Password?</span>
        </a>
        <Button variant="contained" type="submit" disabled={!disabledSubmit}>
          Login
        </Button>
      </form>
      {response.loaded && (
        <Toast code={response.code} message={response.message} />
      )}
    </div>
  );
};

export default LoginForm;
