import { useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

interface Props {
  code: number;
  message: string;
}

const Toast: React.FC<Props> = ({ code, message }) => {
  useEffect(() => {
    switch (code) {
      case 200:
        toast.success(message, {
          position: 'bottom-right',
        });
        break;
      case 400:
      case 401:
      case 404:
        toast.error(message, {
          position: 'bottom-right',
        });
        break;
      case 500:
        toast.error('Server error: ' + message, {
          position: 'bottom-right',
        });
        break;
      default:
        toast.info('Unhandled status code: ' + message, {
          position: 'bottom-right',
        });
        break;
    }
  }, [code, message]);

  return <ToastContainer className="absolute" />;
};

export default Toast;
