import { useEffect, useState } from 'react';

import { useLocation, useNavigate } from 'react-router';
import DynamicList from '@/components/AgencyList';
import { AgencyForm } from '@/components/AgencyForm';
import { getWithExpiry } from '@/utils/localStorage';
import { useDispatch } from 'react-redux';
import Toast from '@/components/Toast';
import { httpResponse } from '@/utils/types';
import { removeStop, setStops } from '@/store/stopSlice';
import { StopForm } from '@/components/StopForm';
import StopList from '@/components/StopList';

const Stops = () => {
  const [redirect, setRedirect] = useState<string>('list');
  const dispatch = useDispatch();

  const navigate = useNavigate();

  const location = useLocation();

  const [response, setResponse] = useState<httpResponse>({
    code: 0,
    loaded: false,
    loading: false,
    message: '',
  });

  const getStops = async () => {
    try {
      const response = await fetch('/api/stops', {
        method: 'GET',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
          Authorization: `Bearer ${getWithExpiry('TOKEN')}`,
        },
      });
      const data = await response.json();
      if (response.ok) {
        console.log(' data ,', data);
        //  setAgencies(data);
        dispatch(setStops(data)); // Set the agencies in Redux state
      } else {
      }
    } catch (error) {
      console.error('Network error:', error);
      // Handle network error, e.g., show a toast
    }
  };

  useEffect(() => {
    getStops();
  }, []);

  const locationHandler = () => {
    const { pathname } = location;
    switch (pathname.split('/')[2]) {
      case 'edit':
        setRedirect('edit');
        break;
      case 'new':
        setRedirect('new');
        break;
      case 'delete':
        setRedirect('delete');
        break;
      default:
        setRedirect('list');
        return;
    }
  };
  useEffect(() => {
    locationHandler();
  }, [location]);
  const deleteStop = async () => {
    try {
      const { search } = location;

      const stopId = search.split('=')[1];

      //setLoading(true);
      const response = await fetch(`/api/stops/${stopId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${getWithExpiry('TOKEN')}`,
        },
      });
      const data = await response.json();

      setResponse({
        code: response.status,
        loaded: true,
        loading: true,
        message: data.message,
      });
      if (!response.ok) {
        throw new Error(`Failed to delete stop: ${response.status}`);
      }

      // Remove agency from Redux store
      dispatch(removeStop(stopId));

      navigate('/stops');
    } catch (error) {
      console.error('Error deleting agency:', error);
    } finally {
      //  setLoading(false);
    }
  };
  useEffect(() => {
    if (redirect == 'delete') {
      deleteStop();
    }
  }, [redirect]);
  const ToastHandler = (result: httpResponse) => {
    setResponse({
      code: result.code,
      loaded: true,
      loading: true,
      message: result.message,
    });
  };
  return (
    <>
      <div className="flex h-full drop-shadow-xl shadow-xl">
        <div className=" h-full border-r-2 flex flex-col py-2">
          {redirect == 'list' && <StopList />}

          {redirect == 'new' && (
            <StopForm update={false} setResponse={ToastHandler} />
          )}
          {redirect == 'edit' && (
            <StopForm update={true} setResponse={ToastHandler} />
          )}
        </div>
      </div>
      {response.loaded && (
        <Toast code={response.code} message={response.message} />
      )}
    </>
  );
};

export default Stops;
