export const setWithExpiry = (key: string, value: any, ttl: number): void => {
  const now = new Date();

  // `item` is an object which contains the original value
  // as well as the time when it's supposed to expire
  const item = {
    value: value,
    expiry: ttl, // ttl is assumed to be in seconds
  };

  localStorage.setItem(key, JSON.stringify(item));
};

export const getWithExpiry = (key: string): any | null => {
  const itemStr = localStorage.getItem(key);

  // If the item doesn't exist, return null
  if (!itemStr) {
    return null;
  }

  const item: { value: any; expiry: number } = JSON.parse(itemStr);
  const now = new Date();

  // Compare the expiry time of the item with the current time
  if (now.getTime() / 1000 > item.expiry) {
    // If the item is expired, delete it from storage and return null
    localStorage.removeItem(key);
    return null;
  }

  return item.value;
};
