import React from 'react';
import {
  TextField,
  Checkbox,
  FormControlLabel,
  Button,
  Box,
} from '@mui/material';

type FormProps<T> = {
  model: T;
  onSubmit: (data: T) => void;
};

export function DynamicForm<T extends object>({
  model,
  onSubmit,
}: FormProps<T>) {
  const [formData, setFormData] = React.useState<Partial<T>>({});

  const handleChange = (field: keyof T, value: any) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData as T);
  };

  React.useEffect(() => {
    // Call handleChange for all default values on component mount
    Object.entries(model).forEach(([key, value]) => {
      handleChange(key as keyof T, value);
    });
  }, [model]);
  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}
    >
      {Object.entries(model).map(([key, value]) => {
        const fieldType =
          typeof value === 'number'
            ? 'number'
            : typeof value === 'boolean'
            ? 'checkbox'
            : 'text';

        const inputValue = formData[key as keyof T] ?? '';
        return (
          <div key={key}>
            {fieldType === 'checkbox' ? (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={!!formData[key as keyof T]}
                    onChange={(e) =>
                      handleChange(key as keyof T, e.target.checked)
                    }
                  />
                }
                label={key}
              />
            ) : (
              <TextField
                fullWidth
                label={key}
                type={fieldType}
                defaultValue={value}
                onChange={(e) => handleChange(key as keyof T, e.target.value)}
              />
            )}
          </div>
        );
      })}
      <Button type="submit" variant="contained" color="primary">
        Submit
      </Button>
    </Box>
  );
}
