import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { stopType } from '@/utils/types';
interface StopState {
  stops: stopType[];
}

const initialState: StopState = {
  stops: [],
};

const stopSlice = createSlice({
  name: 'stop',
  initialState,
  reducers: {
    setStops: (state, action: PayloadAction<stopType[]>) => {
      state.stops = action.payload;
    },
    addStop: (state, action: PayloadAction<stopType>) => {
      state.stops.push(action.payload);
    },
    removeStop: (state, action: PayloadAction<string>) => {
      state.stops = state.stops.filter((stop) => stop._id !== action.payload);
    },
    editStop: (state, action: PayloadAction<stopType>) => {
      const index = state.stops.findIndex(
        (stop) => stop._id === action.payload._id
      );
      if (index !== -1) {
        state.stops[index] = action.payload;
      }
    },
  },
});

export const { setStops, addStop, removeStop, editStop } = stopSlice.actions;
export default stopSlice.reducer;
