import { AppDispatch, RootState } from '@/store/store';
import { DynamicForm } from './DynamicForm';
import { stopType, httpResponse } from '@/utils/types';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import { IconButton } from '@mui/material';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router';
import { getWithExpiry } from '@/utils/localStorage';
import { addAgency, editAgency } from '@/store/agencySlice';
import { addStop, editStop } from '@/store/stopSlice';

interface stopFormProps {
  update: boolean;
  setResponse: (response: httpResponse) => void;
}

let field = {
  stop_id: '',
  stop_name: '',
  stop_lat: '',
  stop_lon: '',
};
type FieldKeys = keyof typeof field;

export const StopForm = ({ update, setResponse }: stopFormProps) => {
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch<AppDispatch>();

  const { search } = location;
  const stops = useSelector((state: RootState) => state.stops.stops);

  const [model, setModel] = useState<stopType>(field);

  const handleUserSubmit = async (data: stopType) => {
    const stopId = search.split('=')[1];
    console.log(' daaaaaaaaaata ', data);

    let url = '/api/stops';
    if (update) {
      url = url + `/${stopId}`;
    }
    const options: RequestInit = {
      method: update ? 'PUT' : 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${getWithExpiry('TOKEN')}`,
      },
      body: JSON.stringify(data),
    };

    try {
      const response = await fetch(url, options);

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const result = await response.json();

      setResponse({
        code: response.status,
        loaded: true,
        loading: true,
        message: result.message,
      });

      if (update) {
        dispatch(editStop(result));
      } else {
        dispatch(addStop(result));
      }
      navigate(-1);
      console.log(update ? 'Update Success:' : 'Add Success:', result);
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const getStopById = (agencyFiltered: stopType[]) => {
    return agencyFiltered.map((item) =>
      Object.keys(field).reduce((acc, key) => {
        if (key in item) {
          acc[key as FieldKeys] = item[key as FieldKeys];
        }
        return acc;
      }, {} as Pick<stopType, FieldKeys>)
    );
  };

  useEffect(() => {
    if (update) {
      const stopId = search.split('=')[1];
      const stopFiltered = stops.filter((a) => a._id! === stopId);

      const stop = getStopById(stopFiltered);
      setModel(stop[0]);
    }
  }, [update]);
  useEffect(() => {
    const searchParams = new URLSearchParams(window.location.search);

    // Extract lat and lng values
    const lat = searchParams.get('lat');
    const lng = searchParams.get('lng');
    field.stop_lat = lat!;
    field.stop_lon = lng!;
  }, []);

  return (
    <div className="m-4">
      <div className="flex mb-4">
        <IconButton
          aria-label="back"
          size="small"
          onClick={() => {
            navigate(-1);
          }}
        >
          <ArrowBackIosIcon />
        </IconButton>
        <h1 className="my-2"> {update ? 'Edit Stop' : 'Add Stop'}</h1>
      </div>
      <DynamicForm<stopType> model={model} onSubmit={handleUserSubmit} />
    </div>
  );
};
