import { IconButton } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';
import { useState } from 'react';

interface Props {
  expandedHandler: (expand: boolean) => void;
}

const Topbar = ({ expandedHandler }: Props) => {
  const [expand, setExpand] = useState<boolean>(false);
  return (
    <div className="w-full h-14 bg-gray-100 flex items-center">
      <IconButton
        size="large"
        aria-label="menu"
        onClick={() => {
          setExpand((prev) => !prev);
          expandedHandler(expand);
        }}
      >
        {expand ? (
          <MenuIcon fontSize="inherit" />
        ) : (
          <MenuOpenIcon fontSize="inherit" />
        )}
      </IconButton>
      <div>
        <h1> Public transport system</h1>
      </div>
    </div>
  );
};
export default Topbar;
